<template>
  <section class="login">
    <div class="login-logo">
      <img
        src="@/assets/images/logo-big.svg"
        alt="Logo"
        :width="$isMobile ? 170 : 198"
        :height="$isMobile ? 124 : 106"
      />
    </div>
    <form class="login-form" @submit.prevent="login">
      <span
        style="display: block; color: red; font-size: 12px; margin-bottom: 16px"
      >
        {{ errorMsg }}
      </span>
      <div class="login-username">
        <base-input
          :type="'text'"
          :id="'login'"
          :placeholder="'Логин'"
          :required="true"
          @input="errorMsg = ''"
          v-model="username"
        />
      </div>
      <div class="login-password">
        <base-input
          :type="'password'"
          :id="'password'"
          :placeholder="'Пороль'"
          :required="true"
          @input="errorMsg = ''"
          v-model="password"
        />
      </div>
      <div class="login-signIn">
        <base-button :type="'filled'"> Войти </base-button>
      </div>
      <div class="login-report">
        <base-link
          :to="{ name: 'index' }"
          :size="16"
          :lineHeight="19"
          :color="'#ACACAC'"
          :align="'center'"
        >
          Если забыли пароль, обратитесь к администратору
        </base-link>
      </div>
    </form>
  </section>
</template>

<script>
import BaseInput from "@/components/common/Base.Input.vue";
import BaseButton from "@/components/common/Base.Button.vue";
import BaseLink from "@/components/common/Base.Link.vue";

export default {
  name: "Login",
  components: {
    BaseInput,
    BaseButton,
    BaseLink,
  },
  data() {
    return {
      username: "",
      password: "",
      passStatus: false,
      errorMsg: "",
    };
  },
  methods: {
    login() {
      const data = {
        username: this.username,
        password: this.password,
      };
      this.$api
        .post(`${this.$api.defaults.API}/login/`, data)
        .then((res) => {
          const user = res.data.user;
          user.token = res.data.token;
          this.$store.dispatch("setUser", user);
          localStorage.setItem("user", JSON.stringify(user));
          this.$api.defaults.headers.Authorization = `Token ${user.token}`;
          let name = this.$route.next || "index";
          this.$router.push({ name: name });
        })
        .catch((err) => {
          if (err.response.data) this.errorMsg = "Неверный Логин или Пороль";
        });
    },
  },
};
</script>

